.navbar {
    height: 70px;
    padding: 20px 35px;
    box-shadow: 0 2.98px 7.45px rgba(0, 0, 0, 0.1);
}

.airbnb-logo {
    max-width: 100px;
}

.hero {
    display: flex;
    flex-direction: column;
}

.hero-img {
    max-width: 75%;
    align-self: center;
}

.hero-heading {
    margin-bottom: 16px;
    font-size: 2.3rem;
}

.hero-text {
    margin-top: 0;
    font-size: 1.3rem;
}

.card-slider {
    display: flex;
    flex-wrap: nowrap;
    gap: 20px;
    overflow-x: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.card-slider::-webkit-scrollbar {
    display: none;
}

.card {
    position: relative;
    width: 175px;
    font-size: 12px;
    flex: 0 0 auto;         
    display: flex;          
    flex-direction: column; 
}

.img-box {
    max-width: 175px;
    height: 235px;
    overflow: hidden;
}

.card-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 9px;
    margin-bottom: 9px;
}

.card-stats {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 3px;
    margin-top: 5px;
}

.star-logo {
    height: 14px;
}

.card-title {
    overflow: hidden;
    text-overflow: ellipsis;
}

.card-price {
    margin-top: auto;
    font-weight: bold;
}

.card-badge {
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}